@import "./utils/fonts";
@import "./utils/variables.scss";
@import "./utils/mixins.scss";

* {
  box-sizing: border-box;
}

html,
body,
div,
span,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
p,
a,
article,
aside,
footer,
header,
nav,
section {
  margin: 0;
  padding: 0;
  border: 0;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

button {
  cursor: pointer;
}

.App {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
}

.no-scroll {
  overflow: hidden;
}

.main {
  flex-grow: 1;
  width: 100%;
  margin-inline: auto;
  max-width: 1440px;
  padding-inline: 20px;
  @include onTablet {
    padding-inline: 80px;
  }

  @include onDesktopMd {
    padding-inline: 152px;
  }

  @include onDesktopLg {
    padding-inline: 152px;
  }

  &--blur {
    filter: blur(5px);
    pointer-events: none;
    user-select: none;
  }
}

.shop-by-category {
  margin-top: 80px;

  &_body {
    margin-top: 24px;
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    column-gap: 16px;

    @include onTablet {
      grid-template-columns: repeat(3, 1fr);
    }
  }

  &_box {
    display: flex;
    flex-direction: column;
  }

  &_img {
    display: flex;
    padding-bottom: 50%;
    transition: transform 0.8s ease;
    background-size: cover;
    background-repeat: no-repeat;

    &:hover {
      transform: scale(1.05);
    }

    &--phones {
      background-image: url("./icons/category_phones.png");
    }
  }
}

.category_img-box {
  max-height: 378px;
}

.details {
  margin-top: 24px;
}

.loader {
  display: flex;
  justify-content: center;
  grid-column: 1 / 24;
}

.btn-cart {
  color: #e7e1e1;
  border: 1px solid $icons-color;
  background-size: 300% 300%;
  transition: color 1s ease, background-position 1s ease;

  background-image: linear-gradient(to top, #efebeb 50%, $primary-color 0);
  background-position: top;

  &--animation {
    background-position: bottom;
    color: #7f8883;
  }
}

.btn-fav {
  border: 1px solid $icons-color;
  background-color: rgb(190, 199, 207);
  background-image: url("./icons/icon_favorites.svg");
  background-repeat: no-repeat;
  background-position: center;
  transition: background-image 0.5s ease-in-out;

  &--added {
    background-image: url("./icons/icon_favorites_selected.svg");
  }

  &:hover {
    box-shadow: 0 3px 13px 0 rgba(23, 32, 49, 0.26);
  }
}

.page-btns {
  background-color: #fff;
  border: 1px solid $icons-color;
  user-select: none;
  transition: box-shadow 400ms ease-in-out;

  &:hover {
    box-shadow: 0 0 2px 0.5px $primary-color;
  }
}

.products-wrapp {
  margin-bottom: 64px;
  display: flex;
  justify-content: center;
}


.register-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 2rem;
  background-color: #f5f5f5;
}

.register-page__title {
  font-size: 2rem;
  color: #333;
  margin-bottom: 1.5rem;
}

.register-page__link {
  font-size: 1rem;
  color: #007bff;

  a {
    color: #007bff;
    text-decoration: none;
    font-weight: bold;

    &:hover {
      text-decoration: underline;
    }
  }
}

